import React, { Component } from 'react';
import '../css/Projects.css';
import Project from '../../project/Project';
import Kodflix from '../../img/kodflix.png';
import CalendarImage from '../../img/calendar screenshot2.png'

export default class Projects extends Component {
    render() {
        return(
            <div className='projects'>
                <div className='projects-main-title'>Projects</div>
                <div className='all-projects'>
                    {
                        projects.map((project) => (
                            <Project key={project.src} project={project} />
                        ))
                    }
                </div>
            </div>
        );
    }
}

const projects = [
    {
        title: 'Kodflix',
        image: Kodflix,
        link: 'https://osman-kodflix.herokuapp.com/',
        action: 'View',
        src: 'https://github.com/maruntav/kodflix'

    },
    {
        title: 'Calendar',
        image: CalendarImage,
        link: 'https://github.com/maruntav/Calendar2/blob/master/readME.md',
        action: 'Download',
        src: 'https://github.com/maruntav/Calendar2'
    }
];