import React from 'react';

const project = (props) => (
    <div className='project'>
        <div className='project-image'>
            <img src={props.project.image} alt='Osman' />
        </div>
        <div className='project-options'>
            <div className='project-options-view'><a href={props.project.link}>{props.project.action}</a></div>
            <div className='project-view-source'><a href={props.project.src}>Source Code</a></div>
        </div>
    </div>
);

export default project;